.about {
  padding-bottom: 20px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.498);
  margin-bottom: 40px;
}

.about__caption {
  text-align: left;
  font-size: 55px;
  line-height: 130%;
  font-weight: 400;
}

.about__list {
  list-style: none;
  margin: 0;
  padding: 0;

  display: flex;
  width: 100%;
  justify-content: space-around;
}

.about__photo {
  border-radius: 50%;
}

.about__position {
  color: $blue;
  font-style: italic;
}
