.pride__caption {
  text-align: left;
  color: $blue;
  font-size: 60px;
  font-weight: 400;
}

.pride__list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.pride__item {
  width: 100%;
  display: flex;
  padding: 20px 0;
  gap: 20px;
  justify-content: space-between;
  border-top: 1px solid rgba(0, 0, 0, 0.498);
  border-bottom: 1px solid rgba(0, 0, 0, 0.498);

  font-size: 20px;
  line-height: 120%;
}

.pride__item-caption {
  margin: 0;
  font-size: 30px;
  font-weight: 400;
  color: $blue;
  text-transform: capitalize;
}

.pride__item-description {
  margin: 0;
  width: 33%;
  text-align: left;
}

.pride__fact {
  flex-shrink: 0;
  max-width: 15%;
  & span {
    font-size: 60px;
    color: $blue;
    font-weight: 900;
  }
}

.pride__img {
  width: 250px;
}
