.header {
  position: relative;
  z-index: 1;
}
.header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;

  &--bottom {
    justify-content: flex-end;
  }
}

.header__bottom {
  width: 100%;
  padding-bottom: 80px;
  background: rgb(47, 47, 226);
  background: linear-gradient(90deg, rgba(58, 58, 255, 0.757) 0%, rgba(255, 255, 255, 0.498) 100%);
}
