.slider {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 30px;
  margin-bottom: 100px;
}

.slider__item {
  flex-shrink: 0;
}

.slider__item .img__bg {
  height: 350px;
}

.slider__item--main .img__bg {
  height: 500px;
}

.slider__item--main .img__bg img {
  height: 100%;
}

.slider__item .img__bg img {
  height: 100%;
}

.slider__item {
  width: 33%;
}

.slider__description {
  text-align: left;
  margin: 0;
  & address,
  & span {
    font-style: italic;
    color: $blue;
    text-align: left;
  }
}

.slider__caption {
  text-align: left;
  font-size: 30px;
  line-height: 120%;
  margin-bottom: 0;
}

.slider__project {
  margin: 0;
  padding-right: 60px;
}

