.main {
  flex: 1 1 auto;
  text-align: center;

  @media (max-width: 500px) {
    padding-top: 60px;
  }
}

.main__photo {
  width: 100%;
  margin-top: -100px;
}

.main__subcaption {
  text-align: left;
  font-size: 55px;
  line-height: 130%;
  font-weight: 400;
}



figure {
  position: relative;
  width: 100%;
  margin: 0;
}

figcaption {
  width: 100%;
  position: absolute;
  top: 0;
  font-size: 20px;
  color: #fff;
}

figcaption p {
  text-align: left;
}

.figure__subcaption {
  font-style: italic;
  opacity: 0.5;
}
