.navigation {
  flex-grow: 0;
  padding: 20px 0;
}

.navigation__link {
  display: inline-block;
  text-decoration: none;
  color: #000;
  font-size: 18px;
  padding: 0 10px;
  line-height: 140%;
  border-right: 1px solid rgb(176, 172, 172);

  border-bottom: 1px solid transparent;
  border-top: 1px solid transparent;
  transition: 0.5s;

  &--current {
    font-weight: 900;
    color: #000000;
    transition: 0.5s;
  }

  &--btn {
    color: rgb(76, 76, 229);
  }

  &:hover {
    border-bottom: 1px solid rgb(176, 172, 172);
    border-top: 1px solid rgb(176, 172, 172);
  }
}

.navigation__item:first-child .navigation__link {
  border-left: 1px solid transparent;

  &:hover {
    border-left: 1px solid rgb(176, 172, 172);
  }
}

.navigation__item:last-child .navigation__link {
  border: 1px solid transparent;

  &:hover {
    border-bottom: 1px solid rgb(176, 172, 172);
    border-top: 1px solid rgb(176, 172, 172);
    border-right: 1px solid rgb(176, 172, 172);
  }
}

.header__wrapper--bottom .navigation__item:first-child .navigation__link {
  border-bottom: 1px solid transparent;
  border-top: 1px solid transparent;

  &:hover {
    border-left: 1px solid transparent;
  }
}


.navigation__list {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  min-height: 40px;
  align-items: center;
}

.navigation__menu {
  display: none;

  @media (max-width: 500px) {
    display: block;
    position: absolute;
    top: 15px;
    right: 15px;

    height: 30px;
    width: 30px;
    border-radius: 50%;
    cursor: pointer;

    &--on {
      background-image: url("../img/svg/menu-on.svg");
      background-position: center center;
      background-repeat: no-repeat;
    }

    &--off {
      background-image: url("../img/svg/menu-off.svg");
      background-position: center center;
      background-repeat: no-repeat;
    }
  }
}
